<template>
  <v-container grid-list-md>
    <v-layout wrap>
      <v-flex xs12>
        <h3 class="headline">Import</h3>
      </v-flex>

      <v-flex xs12 md6 lg4>
        <v-btn
          :disabled="working"
          outline
          block
          color="primary"
          @click="importData(emptyProject)"
        >
          Empty
        </v-btn>
      </v-flex>
      <v-flex xs12 md6 lg4>
        <v-menu :disabled="working" bottom offset-y full-width>
          <v-btn slot="activator" :disabled="working" outline block color="primary">Examples</v-btn>
          <v-list>
            <v-list-tile v-for="(example, i) in examples" :key="'example' + i" @click.prevent>
              <v-list-tile-title @click="importData(example.data)" v-text="example.title" />
            </v-list-tile>
          </v-list>
        </v-menu>
      </v-flex>
      <v-flex xs12 md6 lg4>
        <v-btn
          :disabled="working"
          outline
          block
          color="primary"
          @click="uploadJSON"
        >
          File
        </v-btn>
      </v-flex>

      <v-flex xs12 pt-5>
        <h3 class="headline">Export</h3>
      </v-flex>

      <v-flex xs12 sm4>
        <v-btn :disabled="working" outline block color="primary" @click="downloadJSON">JSON</v-btn>
      </v-flex>
      <v-flex xs12 sm4>
        <v-btn :disabled="working" outline block color="primary" @click="downloadScript">Python 2 script</v-btn>
      </v-flex>
      <v-flex xs12 sm4>
        <v-btn :disabled="working" outline block color="primary" @click="downloadAddressingPlan">Addressing plan</v-btn>
      </v-flex>

      <v-flex xs12 pt-4>
        <h3>Image</h3>
      </v-flex>

      <v-flex xs12 sm6 lg3>
        <v-text-field
          :disabled="working"
          label="Width on screen"
          type="number"
          :min="0"
          :step="0.1"
          v-model.number="imageMetadata.widthScreenCm"
          suffix="cm"
        />
      </v-flex>
      <v-flex xs12 sm6 lg3>
        <v-text-field
            :disabled="working"
            label="Height on screen"
            type="number"
            :min="0"
            v-model.number="imageMetadata.heightScreenCm"
            suffix="cm"
          />
      </v-flex>
      <v-flex xs12 sm6 lg3>
        <v-text-field
          :disabled="working"
          label="Width on paper"
          type="number"
          :min="0"
          v-model.number="imageMetadata.widthPaperCm"
          suffix="cm"
        />
      </v-flex>
      <v-flex xs12 sm6 lg3>
        <v-text-field
          :disabled="working"
          label="Height on paper"
          type="number"
          :min="0"
          v-model.number="imageMetadata.heightPaperCm"
          suffix="cm"
        />
      </v-flex>

      <v-flex xs12 sm6 md4>
        <v-text-field
          :disabled="working"
          label="Width"
          type="number"
          :min="0"
          v-model.number="imageMetadata.widthPx"
          suffix="px"
        />
      </v-flex>
      <v-flex xs12 sm6 md4>
        <v-text-field
          :disabled="working"
          label="Height"
          type="number"
          :min="0"
          v-model.number="imageMetadata.heightPx"
          suffix="px"
        />
      </v-flex>

      <v-flex xs12 sm12 md4 mt-2>
        <v-btn
          :disabled="working"
          outline
          block
          color="primary"
          @click= "downloadImageStart"
        >
          Render image
        </v-btn>
      </v-flex>

      <v-slide-y-transition mode="out-in">
        <v-flex v-if="sortedLog.length" xs12 pt-5>
          <section>
            <h3 class="headline">Log</h3>

            <v-list>
              <v-list-tile v-for="(l, i) in sortedLog" :key="'export_log_' + i" avatar @click.prevent>
                <v-list-tile-action>
                  <v-checkbox v-model="logCbs[i]" color="primary" hide-details />
                </v-list-tile-action>
                <v-list-tile-content @click="$set(logCbs, i, !logCbs[i])">
                  <v-list-tile-title v-text="l.msg" />
                </v-list-tile-content>
                <v-list-tile-avatar @click="selectInCanvas(l.item.id)">
                  <v-icon :color="l.severity" v-text="'$vuetify.icons.' + l.severity" />
                </v-list-tile-avatar>
              </v-list-tile>
            </v-list>

            <v-btn outline color="primary" @click="selectInCanvas()">Select in the Canvas</v-btn>
          </section>
        </v-flex>
      </v-slide-y-transition>

      <div style="height: 0px; width: 0px; overflow: hidden;">
        <input ref="fileInput" type="file" :accept="importAccept" @input="uploadFile" @change="uploadFile" />
        <VisCanvas v-if="visCanvasOn" ref="visCanvas" @ready="downloadImageFinish" />
      </div>
    </v-layout>
  </v-container>
</template>

<script>
import AddressingPlan from '@/builder/AddressingPlan'
import Builder from '@/builder'
import VisCanvas from '@/components/vis/VisCanvas'
import exporter from '@/exporter'
import importScript from '@/importScript'
import { mapGetters } from 'vuex'

import exampleEmpty from '@/examples/empty'
import exampleMedium1C from '@/examples/medium_1_controller'
import exampleMedium2C from '@/examples/medium_2_controllers'
import exampleTiny from '@/examples/tiny'
import exampleTinyController from '@/examples/tiny_controller'
import exampleTinyMininetConf from '@/examples/tiny_mininet_conf'
import exampleTinyPhysicalInterface from '@/examples/tiny_physical_interface'
import exampleTinyTC from '@/examples/tiny_tc'

function download (filename, mimeOrHref, data) {
  const href = mimeOrHref && data
    ? `data:${mimeOrHref},${encodeURIComponent(data)}`
    : mimeOrHref

  const element = document.createElement('a')
  element.setAttribute('href', href)
  element.setAttribute('download', filename)
  element.style.display = 'none'

  document.body.appendChild(element)
  element.click()
  document.body.removeChild(element)
}

const logPriority = [
  'error', 'warning', 'info'
].reduce((acc, v, i) => {
  acc[v] = i
  return acc
}, {})

export default {
  name: 'Export',
  components: { VisCanvas },
  data: () => ({
    log: [],
    logCbs: [],
    visCanvasOn: false,
    imageMetadata: {
      width: 2000,
      height: 1000,
      scale: 1,
      screenDpcm: 38,
      paperDpcm: 120,

      get widthPx () {
        return Math.ceil(this.width * this.scale)
      },
      set widthPx (v) {
        this.scale = v / this.width
      },
      get heightPx () {
        return Math.ceil(this.height * this.scale)
      },
      set heightPx (v) {
        this.scale = v / this.height
      },

      get widthScreenCm () {
        return Math.round((
          Math.ceil(this.width * this.scale) / this.screenDpcm
        ) * 100) / 100
      },
      set widthScreenCm (v) {
        this.scale = v * this.screenDpcm / this.width
      },
      get heightScreenCm () {
        return Math.round((
          Math.ceil(this.height * this.scale) / this.screenDpcm
        ) * 100) / 100
      },
      set heightScreenCm (v) {
        this.scale = v * this.screenDpcm / this.height
      },

      get widthPaperCm () {
        return Math.round((
          Math.ceil(this.width * this.scale) / this.paperDpcm
        ) * 100) / 100
      },
      set widthPaperCm (v) {
        this.scale = v * this.paperDpcm / this.width
      },
      get heightPaperCm () {
        return Math.round((
          Math.ceil(this.height * this.scale) / this.paperDpcm
        ) * 100) / 100
      },
      set heightPaperCm (v) {
        this.scale = v * this.paperDpcm / this.height
      }
    },
    emptyProject: exampleEmpty,
    examples: [{
      title: 'Tiny without controller',
      data: exampleTiny
    }, {
      title: 'Tiny with controller',
      data: exampleTinyController
    }, {
      title: 'Tiny with physical interface',
      data: exampleTinyPhysicalInterface
    }, {
      title: 'Tiny with traffic control',
      data: exampleTinyTC
    }, {
      title: 'Tiny with Mininet settings',
      data: exampleTinyMininetConf
    }, {
      title: 'Medium with 1 controller',
      data: exampleMedium1C
    }, {
      title: 'Medium with 2 controllers',
      data: exampleMedium2C
    }]
  }),
  watch: {
    data () {
      this.updateImageMetadata()
    }
  },
  computed: {
    ...mapGetters('topology', [
      'boundingBox',
      'data'
    ]),
    working: {
      get () {
        return !!this.$store.state.working
      },
      set (value) {
        if (value) {
          this.$store.commit('clearAlert')
        }
        this.$store.commit('setWorking', { working: !!value })
      }
    },
    sortedLog () {
      return [...this.log].sort(
        ({ severity: a }, { severity: b }) =>
          logPriority[a] - logPriority[b]
      )
    },
    importers () {
      function json (json) {
        return { data: JSON.parse(json), log: [] }
      }
      function python (script) {
        return importScript(script)
      }
      return {
        '.json': json,
        '.py': python,
        'application/json': json,
        'application/x-python-code': python,
        'text/x-python': python,
        json,
        python
      }
    },
    importAccept () {
      return Object.keys(this.importers)
        .filter(key => /(^.|\/)/.test(key))
        .join(',')
    }
  },
  methods: {
    showAlert (type, text) {
      this.$store.commit('setAlert', { type, text })
    },
    uploadJSON () {
      const input = this.$refs.fileInput
      input.click()
    },
    uploadFile () {
      this.working = true

      const input = this.$refs.fileInput
      const file = input.files[0]
      input.value = ''

      // Some browsers emit input, some change and some both.
      // Return if the file was already collected by the other event handler.
      if (!file) {
        this.working = false
        return
      }

      const fr = new FileReader()
      fr.readAsBinaryString(file)
      fr.onloadend = async () => {
        try {
          const stringToImport = this.importers[file.type] ||
            this.importers[file.name.replace(/^.*(?=\.)/, '')]
          if (stringToImport) {
            const str = fr.result
            const { data, log } = stringToImport(str)
            this.log = log
            if (stringToImport === this.importers.python) {
              await this.confirmImport(data, '<p>Importing scripts is highly unreliable. Imported project can be <strong>incomplete</strong> or <strong>dysfunctional</strong>.</p>')
            } else {
              await this.confirmImport(data)
            }
          } else {
            this.showAlert('error', `Unknown file format: “${file.type}”.`)
          }
        } catch (error) {
          console.error(error)
          this.showAlert('error', 'Import failed.')
        } finally {
          this.working = false
        }
      }
    },
    async importData (data) {
      this.working = true
      await this.confirmImport(data)
      this.working = false
    },
    async confirmImport (importData, text) {
      const confirmed = await this.$confirm(
        (text || '') +
        '<p>This will <strong>erase all your work</strong> (except what you have exported).<br/>Are you sure you want to continue?</p>',
        {
          buttonFalseText: 'Keep existing project',
          buttonTrueText: 'Import',
          icon: this.$vuetify.icons.warning,
          title: 'Warning',
          width: 600
        }
      )
      if (confirmed) {
        this.$store.commit('topology/importData', importData)
        this.showAlert('success', 'Successfully imported.')
      } else {
        this.showAlert('info', 'Import canceled.')
      }
    },
    selectInCanvas (id) {
      let ids
      if (id) {
        ids = [id]
      } else if (this.logCbs.some(cb => cb)) {
        ids = this.logCbs
          .map((cb, i) => cb ? i : null)
          .filter(i => i !== null)
          .map(i => this.sortedLog[i].item.id)
      } else {
        ids = this.sortedLog.map(l => l.item.id)
      }

      this.$router.push({
        name: 'Canvas',
        params: { ids: ids.join(',') }
      })
    },
    downloadJSON () {
      try {
        this.working = true

        const json = JSON.stringify(exporter.exportData(this.data), undefined, 4)
        this.showAlert('success', 'Successfully exported.')
        download(this.getFilename('json'), 'application/json;charset=utf-8', json)
      } catch (error) {
        console.error(error)
        this.showAlert('error', 'Export failed.')
      } finally {
        this.working = false
      }
    },
    downloadScript () {
      try {
        this.working = true
        this.log = []

        const builder = new Builder(exporter.exportData(this.data))
        this.log = builder.log
        const script = builder.build()
        this.showAlert('success', 'Script built.')
        download(this.getFilename('py'), 'text/x-python;charset=utf-8', script)
      } catch (error) {
        console.error(error)
        this.showAlert('error', 'Script was not built.')
      } finally {
        this.working = false
      }
    },
    downloadImageStart () {
      this.working = true
      this.visCanvasOn = true
    },
    downloadImageFinish () {
      // The timeout prevents glitches like missing node icons, especially in Firefox.
      window.setTimeout(async () => {
        try {
          const { blob, sizeString } = await this.$refs.visCanvas.toBlob(
            this.imageMetadata.scale,
            progress => {
              this.$store.commit('setWorking', {
                working: true,
                curr: progress,
                max: 1
              })
            }
          )
          this.visCanvasOn = false

          this.showAlert('success', `Image rendered, size: ${sizeString}.`)
          const url = URL.createObjectURL(blob)
          download(this.getFilename('png'), url)
          URL.revokeObjectURL(url)
        } catch (error) {
          console.error(error)
          this.showAlert('error', `Image rendering failed. Probably too large image for this browser.`)
        } finally {
          this.working = false
        }
      }, 100)
    },
    downloadAddressingPlan () {
      try {
        this.working = true
        this.log = []

        const ap = new AddressingPlan(exporter.exportData(this.data))
        ap.build()
        ap.savePDF(this.data.projectName, this.getFilename('pdf'))

        this.showAlert('success', 'Addressing plan built.')
      } catch (error) {
        console.error(error)
        this.showAlert('error', 'Addressing plan was not built.')
      } finally {
        this.working = false
      }
    },
    getFilename (extension) {
      return `${this.data.projectName || 'mininet_network'}.${extension}`
    },
    updateImageMetadata () {
      const bb = this.boundingBox()
      this.imageMetadata.scale = 1
      this.imageMetadata.width = bb.width
      this.imageMetadata.height = bb.height
    }
  },
  mounted () {
    this.updateImageMetadata()
  }
}
</script>

<style scoped>
